@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;

  box-sizing: border-box;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  height: 100vh;
  font-family: 'Montserrat', sans-serif;
}

.Game {
  height: 30vh;
  width: 90%;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  margin-bottom: 2rem;
}

.Button--reset {
  padding: 1.2rem;
}

.loading {
  font-size: 1.5rem;
}

.Scoreboard {
  height: 30vh;
  width: 90%;
  max-width: 450px;
  min-width: 300px;
  border-radius: 8px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  font-size: 2rem;

  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

  margin-bottom: 2rem;
}

.Score {
  margin-bottom: 5px;
}

.Card {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

  height: 90%;
  width: 33%;
  max-width: 150px;
  min-width: calc(320px / 3);

  margin: 0 0.5rem;
  padding: 5px;

  cursor: pointer;

  transition: -webkit-transform 0.2s ease-out;

  transition: transform 0.2s ease-out;

  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;

  border-radius: 2px;
}

.Card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.Card__image {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

